class Turntable {

  constructor() {

    this.lotteryBtn = document.querySelector('#lotteryBtn');
    this.obj = document.querySelector('#turntableBack');
    this.isAward = document.querySelector('#isAward');
    this.list = document.querySelector('#list');
    this.jackpotList = document.querySelector('#jackpotList');
    this.jackpotListUl = document.querySelectorAll('#jackpotList ul');
    this.residue = document.querySelector('#residue');
    this.closeIsAward = document.querySelector('#closeIsAward');
    this.state = {
      timer: null // 定时器
    };
    this.setting = {
      isFakeData: false, // 用户中奖是否自动填存假数据
      listNum: 20, // 生成假数据的条数
      maxLotteryNum: 0, // 当天可抽奖次数
      lotteryNum: 0, // 抽奖次数
      maxSpeed: 30, // 默认最大速度
      speed: 30, // 默认速度
      subtractSpeed: 10, // 每圈过后递减速度
      rotateNum: 0, // 起始角度
      angle: 360, // 一圈的度数
      count: 0, // 圈数
      resultNum: 0, // 倒数第几圈的时候弹出中奖提示
      scrollLength: 5, // 满足5个中奖记录才滚动，不满就不滚动
      moveTargetUrl: 'http://www.caicui.com'
    };

  }

  init() {

    // 获奖用户
    this.listRoll();

    // 绑定事件
    this.bindEvent();

  }

  getUserAwardNum() {

    let _this = this;
    let s = _this.setting;
    let $tipH1 = $('#tip h1');
    let $tipH2 = $('#tip h2');
    let $awardingList = $('#awardingList');

    $.ajax({
      url: 'http://app.china-ife.com/acca/api/article/getUserAwardNum.do',
      type: "POST",
      async: true,
      data: {
        phone: $.cookie('phone')
      },
      success: function(res, status, xhr) {

        // 返回JSON为字符串
        if (Object.prototype.toString.call(res) == '[object String]') res = JSON.parse(res);

        s.maxLotteryNum = res.respCode; // 赋值当天可以转的次数

        if (res.respCode == 0) {
          $tipH1.html('抽奖机会已经用完了:(');
          $tipH2.html(' ');
          $awardingList.show();
          _this.isAward.classList.remove('hide');
        } else {
          // 中什么奖
          _this.userIsAward();
        }

      },
      error: function(response, status, xhr) {
        alert('网络异常，请稍后再试');
      }
    });

  }

  listRoll() {

    let _this = this;
    let s = this.setting;
    let i = 0;
    let html = '';
    let prizeList = [
      'iphone7一部',
      'ipadmini一部',
      '平衡车一台',
      '旅游基金',
      '任意串讲课',
      '双十二大礼包',
      'OBU代金卷',
      '双十二大礼包',
      '双十二大礼包',
      'OBU代金卷'
    ];
    let isScroll = true;
    let phone, num;

    if (s.isFakeData) {

      while (i < s.listNum) {

        num = Math.floor(Math.random() * 10);

        html += `<li>133****1212抽得${prizeList[num]}</li>`;
        i++;
      }

    } else {

      // 请求接口后的数据,请把数据渲染好直接赋值到html里面
      $.ajax({
        url: 'http://app.china-ife.com/acca/api/article/getAwardList.do',
        type: "POST",
        data: {},
        success: function(res, status, xhr) {

          // 返回JSON为字符串
          if (Object.prototype.toString.call(res) == '[object String]') res = JSON.parse(res);

          if (res.respCode == 0 && res.respDesc == '成功') {

            let _dataList = res.list;

            if (_dataList.length < s.scrollLength) isScroll = false;

            while (i < _dataList.length) {

              phone = _dataList[i].user.phone.replace(/(\d{3})\d{4}(\d{4})/, "$1****$2");

              html += `<li>${phone}抽得${_dataList[i].content}</li>`;
              i++;
            }

            _this.jackpotListUl[1].innerHTML = _this.jackpotListUl[0].innerHTML = html;

          } else {
            alert(res.respDesc);
          }

        },
        error: function(response, status, xhr) {
          alert('网络异常，请稍后再试');
        }
      });

    }

    // 滚动
    if (isScroll) this.startRoll();

  }

  startRoll() {

    let _this = this;
    let _num = 0;


    setInterval(() => {
      _num--;

      if (_this.jackpotListUl[1].offsetTop == -_num) {
        _num = 0;
      }
      _this.jackpotListUl[0].style.transform = `translate3d(0,${_num}px,0)`;
      _this.jackpotListUl[0].style.webkitTransform = `translate3d(0,${_num}px,0)`;
      _this.jackpotListUl[1].style.webkitTransform = `translate3d(0,${_num}px,0)`;
      _this.jackpotListUl[1].style.webkitTransform = `translate3d(0,${_num}px,0)`;

    }, 1000 / 16);

  }

  bindEvent() {


    let _this = this;
    let _isAward = true;
    let s = this.setting;
    let html = '';
    let i, len;

    let $phone = $('#isLogin').find('input').eq(0);
    let $code = $('#isLogin').find('input').eq(1);
    let $getCode = $('#getCode');
    let $startCJ = $('#startCj');
    let $tip = $('#tip');
    let $tipH1 = $('#tip h1');
    let $tipH2 = $('#tip h2');
    let $allAwardList = $('#allAwardList');
    let $awardingList = $('#awardingList');
    let $onTargetList = $('#onTargetList');
    let $allAwardListTbody = $('#allAwardListTbody');
    let $isLogin = $('#isLogin');
    let $closeListAward = $('#closeListAward');
    let $sharePage = $('#sharePage');
    let $closeShareBtn = $('#closeShareBtn');
    let $shareAlert = $('#shareAlert');
    let num = 60;
    let timer = null;

    // 关闭分享弹层
    $closeShareBtn.on('click', () => {
      $shareAlert.hide();
    });

    // 分享页面按钮
    $sharePage.on('click', () => {

      $shareAlert.show();

      $.ajax({
        url: 'http://app.china-ife.com/acca/api/article/addActiveLog.do',
        type: "POST",
        data: {
          activeId: 1,
          phone: $.cookie('phone')
        },
        success: function(res, status, xhr) {

          // 返回JSON为字符串
          if (Object.prototype.toString.call(res) == '[object String]') res = JSON.parse(res);

          // 成功
          if (res.respCode == 1) {
            // 成功
          } else {
            alert(res.respDesc);
          }

        },
        error: function(response, status, xhr) {
          alert('网络异常，请稍后再试');
        }
      });

    });

    // 关闭个人中奖列表
    $closeListAward.on('click', () => {
      $allAwardList.hide();
    });

    // 获取验证码
    $getCode.on('click', () => {

      let _phoneVal = $phone.val();

      $phone.removeAttr('style');

      if (_phoneVal.length != 11) {
        $phone.css('borderBottom', '1px solid #f00');
        return;
      }

      $.ajax({
        url: 'http://app.china-ife.com/acca/api/article/getVcode.do',
        type: "POST",
        data: {
          phone: _phoneVal
        },
        success: function(response, status, xhr) {


          // 返回JSON为字符串
          if (Object.prototype.toString.call(response) == '[object String]') response = JSON.parse(response);

          // 成功
          if (response.respCode == 0 && response.respDesc == '成功') {

            $getCode.html('60s').css({
              'backgroundColor': '#333',
              'color': '#fff'
            });

            timer = setInterval(() => {

              num--;

              $getCode.html(`${num}s`);

              if (num == 0) {
                $getCode.html('获取验证码').removeAttr('style');
                clearInterval(timer);
              }

            }, 1000);

          } else {
            $phone.css('borderBottom', '1px solid #f00');
            alert(response.respDesc);
          }

        },
        error: function(response, status, xhr) {
          $getCode.html('获取验证码').removeAttr('style');
          alert('网络异常，请稍后再试');
        }
      });


    });

    // 登陆按钮
    $startCJ.on('click', () => {

      let _phoneVal = $phone.val();
      let _codeVal = $code.val();

      $startCJ.css('backgroundColor', '#d0d0d0');
      $phone.removeAttr('style');
      $code.removeAttr('style');

      if (_phoneVal.length != 11) {
        $phone.css('borderBottom', '1px solid #f00');
        return;
      }

      if (_codeVal.length != 4) {
        $code.css('borderBottom', '1px solid #f00');
        return;
      }

      $.ajax({
        url: 'http://app.china-ife.com/acca/api/article/chekUser.do',
        type: "POST",
        data: {
          phone: _phoneVal,
          smsVcode: _codeVal
        },
        success: function(res, status, xhr) {

          // 返回JSON为字符串
          if (Object.prototype.toString.call(res) == '[object String]') res = JSON.parse(res);

          if (res.respCode == 0 && res.respDesc == '成功') {
            $.cookie('phone', _phoneVal);
            $isLogin.hide();

            // 抽奖次数
            _this.getUserAwardNum();

          } else {
            $code.css('borderBottom', '1px solid #f00');
            $startCJ.removeAttr('style');
            alert(res.respDesc);
          }
        },
        error: function(res, status, xhr) {
          $startCJ.removeAttr('style');
          alert('网络异常，请稍后再试');
        }
      });

    });

    // 点击旋转转盘
    this.lotteryBtn.addEventListener('click', () => {


      // 本地无手机记录显示登录
      if (!$.cookie('phone')) {
        isLogin.classList.remove('hide');
        return;
      }

      // 如果本地有记录，查询手机号是否中过奖
      _this.getUserAwardNum();

    }, false);

    // 中奖后点击领取
    for (i = 0, len = this.list.children.length; i < len; i++) {

      this.list.children[i].addEventListener('click', function() {

        let __this = this;

        // if (_isAward) {

        if (__this.dataset.id == 'null') {
          window.location.href = s.moveTargetUrl;
          return;
        }

        if (__this.classList.contains('active')) {
          alert('您已经有此课程，请领取其它课程！');
          return;
        }

        $.ajax({
          url: 'http://app.china-ife.com/acca/api/article/getCaicuiAward.do',
          type: "POST",
          data: {
            phone: $.cookie('phone'),
            cardRuleId: __this.dataset.id
          },
          success: function(res, status, xhr) {

            // 返回JSON为字符串
            if (Object.prototype.toString.call(res) == '[object String]') res = JSON.parse(res);

            if (res.state == 'success') {
              __this.classList.add('active');
              // _isAward = !_isAward;
            } else {

              if (res.code == '300002') {
                alert('您已经有此课程，请领取其它课程！');
              } else {
                alert('请稍后刷新页面后重新尝试');
              }

            }
          },
          error: function(res, status, xhr) {
            alert('网络异常，请稍后再试');
          }
        });


        // }
      }, false);

    }

    // 获取用户列表得奖
    $onTargetList.on('click', () => {

      html = '';

      i = 0;

      $.ajax({
        url: 'http://app.china-ife.com/acca/api/article/getUserAwardList.do',
        type: "POST",
        data: {
          phone: $.cookie('phone')
        },
        success: function(res, status, xhr) {

          // 返回JSON为字符串
          if (Object.prototype.toString.call(res) == '[object String]') res = JSON.parse(res);

          if (res.respCode == 0 && res.respDesc == '成功') {

            let _dataList = res.list;

            while (i < _dataList.length) {
              html += `<tr><td>${_dataList[i].createDateShowStr}</td><td>${_dataList[i].content}</td></tr>`;
              i++;
            }

            $allAwardListTbody.html(html);
            $allAwardList.show();

          } else {
            alert(res.respDesc);
          }

        },
        error: function(response, status, xhr) {
          alert('网络异常，请稍后再试');
        }

      });


    });

    // 关闭抽奖结果
    this.closeIsAward.addEventListener('click', () => {

      _this.isAward.classList.add('hide');

      s.maxSpeed = 30;
      s.speed = 30;
      s.rotateNum = 0;
      s.count = 0;

      for (i = 0, len = this.list.children.length; i < len; i++) {
        this.list.children[i].classList.remove('active');
      }

    }, false);

  }

  userIsAward() {

    let _this = this;
    let s = _this.setting;
    let $tipH1 = $('#tip h1');
    let $tipH2 = $('#tip h2');
    let $awardingList = $('#awardingList');



    // s.lotteryNum++;

    // // 抽奖次数上限按钮限制
    // if (s.lotteryNum > s.maxLotteryNum) {
    //   return;
    // }


    // 中什么奖
    $.ajax({
      url: 'http://app.china-ife.com/acca/api/article/getAward.do',
      type: "POST",
      data: {
        phone: $.cookie('phone')
      },
      success: function(res, status, xhr) {

        // 返回JSON为字符串
        if (Object.prototype.toString.call(res) == '[object String]') res = JSON.parse(res);

        if (res.respCode == 0 && res.respDesc == '成功') {
          // 返回内容
          $tipH1.html('恭喜你抽中了');
          $tipH2.html(res.award.content);

          // 每次抽完奖都可以点一次领取
          // _isAward = true;

          // $awardingList.hide();
          $awardingList.show();

          // 打开中奖结果页面
          _this.state.timer = setInterval(() => {
            _this.startRotate();
          }, 1000 / 60);

          // if (res.award.content != '任意一门串讲课') {
          // $awardingList.hide();
          // } else {
          // $awardingList.show();
          // }

        } else {
          alert(res.respDesc);
        }

      },
      error: function(response, status, xhr) {
        alert('网络异常，请稍后再试');
      }
    });


  }

  startRotate() {

    let s = this.setting;

    s.rotateNum += s.speed;

    if (s.rotateNum % s.angle == 0) {
      s.count++;
      s.speed -= s.subtractSpeed;
    }

    // 快转玩时弹出中奖结果
    if (s.count == s.maxSpeed / s.subtractSpeed - s.resultNum) {
      this.isAward.classList.remove('hide');
      this.lotteryBtn.dataset.isrotate = 'false';
    }

    this.obj.style.transform = `rotate(${s.rotateNum % s.angle}deg)`;

    if (s.speed == 0) clearInterval(this.state.timer);

  }

};


export default Turntable;



