  let initialRem = 750 / 100;
  let deviceWidth = oHtml.offsetWidth;
  let fSize = 0;

  oHtml.dataset.dpr = DPR;
  fSize = deviceWidth / initialRem;

  if (fSize > 65) fSize = 64;

  oHtml.style.fontSize = `${fSize}px`;

