let bindEvent = () => {

  let getBtn = DOC.querySelector('#getBtn');
  let shade = DOC.querySelector('#shade');
  let contentHome = DOC.querySelector('#contentHome');
  let contentDetail = DOC.querySelector('#contentDetail');
  let isLogin = DOC.querySelector('#isLogin');
  let globalTip = DOC.querySelector('#globalTip');

  getBtn.onclick = () => {

    shade.classList.remove('hide');

    setTimeout(() => {

      shade.classList.add('shade_active');
      contentHome.classList.add('hide');
      clearInterval(timer);

      setTimeout(() => {

        shade.classList.remove('shade_active');

        setTimeout(() => {
          contentDetail.classList.remove('hide');
          shade.classList.add('hide');
          globalTip.classList.add('hide');
          turntable.init();

        }, 500);

      }, 1000);

    }, 500);

  };

};

let switchTo = () => {

  let defaultNum = -1;
  let oDiv = homeBuilding.children;

  timer = setInterval(() => {


    if (defaultNum != -1) oDiv[defaultNum].classList.remove('active');

    defaultNum++;
    defaultNum = defaultNum % oDiv.length;

    oDiv[defaultNum].classList.add('active');

  }, 2000);

};

let globalScrollTip = () => {

  let globalTip = DOC.querySelector('#globalTip');
  let globalTipUl = globalTip.querySelectorAll('ul');
  let html = '';
  let num = 0;
  let isOff = true;
  let i, len, ulHeight, timer, wrapHeight;

  for (i = 0, len = defaultData.length; i < len; i++) {
    html += `<li>${defaultData[i].phone}&nbsp;${defaultData[i].text}</li>`;
  }

  globalTipUl[0].innerHTML = globalTipUl[1].innerHTML = html;

  ulHeight = globalTipUl[0].offsetHeight;
  wrapHeight = 0.51;

  scrollUl();

  function scrollUl() {

    timer = setTimeout(() => {

      num += wrapHeight;

      if (Math.floor(num) == wrapHeight * len) {
        globalTipUl[1].style.transform = globalTipUl[0].style.transition = `0s`;
        num = 0;
      }else{
        globalTipUl[1].style.transform = globalTipUl[0].style.transition = `.5s`;
      }

      globalTipUl[1].style.transform = globalTipUl[0].style.transform = `translate3d(0,${-num}rem,0)`;

      scrollUl();
    }, 1800);
  }

};

